import React, {Component} from 'react';
import Box from "@material-ui/core/Box";
import {CircularProgress, Grid, Typography} from "@mui/material";

const LoadModuleAsync = (importComponent) => {
    return class extends Component {
        state = {
            component: null
        }

        componentDidMount() {
            importComponent()
                .then(cmp => {
                    this.setState({component: cmp.default});
                });
        }

        render() {
            const C = this.state.component;
            return C ? <C {...this.props}/> : <Grid sx={{height: '100dvh'}} alignItems='center' justifyContent='center'>
                <Box sx={{height: 'inherit', display: 'flex', alignItems: 'center', justifyContent: 'center', gap: 10}}>
                    <CircularProgress />
                    <Typography variant='h5'>Loading...</Typography>
                </Box>
            </Grid>;
        }
    }
};

export default LoadModuleAsync;